import React from 'react';
import {connect} from "react-redux";

export default function single_page_information(onject) {
    return (
        <div className="container-wrap">
            <div className="project-info">
                <h1>{onject.title}</h1>
                <p>{onject.describe}
                </p>
                <h5>Client: <span>{onject.client}</span></h5>
                <h5>Date: <span>{onject.Date}</span></h5>
                <h5>Share: Facebook , Twitter , Pinterest</h5>
            </div>
            <div className="project-image">
                <img src={onject.img} alt=""/>
            </div>
        </div>
    )
}