import React from 'react';
import Footer from '../components/footer';
import "../styles/single-project.css";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleRight , faAngleLeft } from '@fortawesome/free-solid-svg-icons';
import single_page_information from '../components/single-project-information';
class Single_page extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            number_value: +document.URL.split("id=")[1],
            count: this.props.products.length,
            //url_string: "" + +this.number_value
        }
    }
    componentDidMount() {
        this.setState({number_value: document.URL.split("id=")[1]});
        let btn = document.querySelector(".nextProject");
        let btn2 = document.querySelector(".prevProject");
        if (+this.state.number_value >= this.state.count - 1 ) {
            btn.style.opacity = "0";
            btn.style.zIndex = "-1";
        }else {
            btn.style.opacity = "1";
            btn.style.zIndex = "1";
        }
        if (this.state.number_value <= 0) {
            btn2.style.opacity = "0";
            btn2.style.zIndex = "-1";
        }else {
            btn2.style.opacity = "1";
            btn2.style.zIndex = "1";
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        let btn = document.querySelector(".nextProject");
        let btn2 = document.querySelector(".prevProject");
        if (this.state.number_value >= this.state.count - 1  ) {
            btn.style.opacity = "0";
            btn.style.zIndex = "-1";
        }else {
            btn.style.opacity = "1";
            btn.style.zIndex = "1";
        }
        if (this.state.number_value <= 0) {
            btn2.style.opacity = "0";
            btn2.style.zIndex = "-1";
        }else {
            btn2.style.opacity = "1";
            btn2.style.zIndex = "1";
        }
    }

    nextProject() {
        this.setState({number_value: this.state.number_value = +this.state.number_value + +1})
    }
    previousProject() {
        this.setState({number_value: this.state.number_value = +this.state.number_value - +1})
    }

    render() {
        return (
            <div>
                <header id="header_">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="menu">
                                <div className="logo">
                                    <img src="" alt=""/>
                                </div>
                                <ul className="nav">
                                    <li><Link to="/">Home</Link></li>
                                    <li><Link to="/about/">About</Link></li>
                                    <li><Link to="/contacts/">Contact</Link></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </header>
                <section id="project-info">
                    <div className="container-x">
                        {single_page_information(this.props.products[this.state.number_value])}
                    </div>
                </section>
                <section id="nav-single-project">
                    <div className="container-x">
                        <div className="container-wrap">
                            <Link to={"/single-project/?id="+ (+this.state.number_value - +1)} className="prevProject" onClick={this.previousProject.bind(this)}><FontAwesomeIcon icon={faAngleLeft} /><span>Previous project</span></Link>
                            <Link to="/home/" className="show-all-project">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                            </Link>
                            <Link to={"/single-project/?id="+ (+this.state.number_value + +1)} className="nextProject" onClick={this.nextProject.bind(this)}><span>next project</span> <FontAwesomeIcon icon={faAngleRight} /></Link>
                        </div>
                    </div>
                </section>
                <Footer />
            </div>
        )
    }
}
export default connect(
    state => ({
        products: state
    }),
    dispatch => ({})
)(Single_page);