function products(state = initialState,action) {
    if (action.type === "ADD_TRACK") {
        return [
            ...state,
            action.payload
        ];
    }
    return state;
}

const store = createStore(products);

ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>
    ,
    document.getElementById('root')
);

