import React , {Component} from 'react';

import "../styles/photobox.css"

import $ from 'jquery';
import Rnum from 'react-jquery-number-animate';
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
//Images
import sign from '../img/sign.png';
import portfolio from '../img/portfolio.png';
import clock from '../img/clock.png';
import star from '../img/star.png';
import like from '../img/like.png';
import look from '../img/look.png';
import diamond from '../img/diamond.png';
import arc from '../img/arc.png';
import phone from '../img/phone.png';
import joy from '../img/joy.png';
import plane from '../img/plane.png';
import start2 from '../img/star2.png';
import wand from '../img/wand.png';
import bucket from '../img/bucket.png';
import sponsor1 from '../img/1.png';
import sponsor2 from '../img/2.png';
import sponsor3 from '../img/3.png';
import sponsor4 from '../img/4.png';

//components
import Footer from '../components/footer';
import Slider from '../components/slider';

class MainPage extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            long: 0, //90
            medium: 0, //73
            mini: 0, //56
            scroll: 0,
            repeat: 1,
            max_new: 8,
            showOrHide: true
        };
    }

    componentDidMount() {
        $(window).scroll(function () {
            $("#skills").each(function () {
                let imagePos = $(this).offset().top;
                let topOfWindow = $(window).scrollTop();
                if (imagePos < topOfWindow + 580 ) {
                    $("#mini").addClass("animateLine");
                    $("#long").addClass("animateLineLong");
                    $("#medium").addClass("animateLineMedium");
                }
            });
            $("#spells").each(function () {
                let imagePos = $(this).offset().top;
                let topOfWindow = $(window).scrollTop();
                if (imagePos < topOfWindow + 580 ) {
                    $(".item-animation-1").addClass("item-fade-animation-1");
                    $(".item-animation-2").addClass("item-fade-animation-2");
                    $(".item-animation-3").addClass("item-fade-animation-3");
                    $(".item-animation-4").addClass("item-fade-animation-4");
                }
            })
        })
    }
    requereS() {
        require("./home");
    }
    getMoreNews() {
        let btn = document.querySelector(".load-more");
        this.setState({max_new: 12});
        this.setState({showOrHide: !this.state.showOrHide});
        if (this.state.showOrHide) {
            btn.style.display = "none";
        }else {
            btn.style.display = "block"
        }
    }
    render() {
        console.log(this.props.products);
        return (
            <div className="wrap">
                <header id="header">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="menu">
                                <div className="logo">
                                    <img src="" alt=""/>
                                </div>
                                <ul className="nav">
                                    <li><Link to="/works/">Works</Link></li>
                                    <li><Link to="/about/" >About</Link></li>
                                    <li><Link to="/contacts/" >Contact</Link></li>
                                </ul>
                            </div>
                            <div className="header-information">
                                <div className="hInfo">
                                    <h1>We Design and Develop</h1>
                                    <p>We are a new design studio based in USA. We have over
                                        20 years of combined experience, and know a thing or two
                                        about designing websites and mobile apps.</p>
                                    <button className="contact-us-btn">contact us</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>
                <section id="about-us">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="about-information">
                                <h2>About us</h2>
                                <p>Divide have don't man wherein air fourth. Own itself make have night won't make.
                                    A you under Seed appear which good give. Own give air without fowl moveth dry first
                                    heaven fruit, dominion she'd won't very all.
                                </p>
                                <img src={sign} alt="sign"/>
                            </div>
                        </div>
                    </div>
                </section>
                <section id="skills">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="about-information_">
                                <div className="skills">
                                    <h2>Professional Skills</h2>
                                    <div className="skills-point">
                                        <h3>UI/UX Design <span className="skill-precent_">
                                            <Rnum start={0} end={53} duration={2000} easing={'linear'} format={true} dot={10} lazy={true} />%
                                        </span></h3>
                                        <div className="off-line">
                                            <div className="color-line" id="mini" >

                                            </div>
                                        </div>
                                    </div>
                                    <div className="skills-point mov">
                                        <h3>web development <span className="skill-precent">
                                            <Rnum start={0} end={93} duration={2000} easing={'linear'} format={true} dot={10} lazy={true} />
                                            %</span></h3>
                                        <div className="off-line">
                                            <div className="color-line mov" id="long" >

                                            </div>
                                        </div>
                                    </div>
                                    <div className="skills-point">
                                        <h3>marketing <span className="skill-precent"><Rnum start={0} end={76} duration={2000} easing={'linear'} format={true} dot={10} lazy={true} />%</span></h3>
                                        <div className="off-line">
                                            <div className="color-line" id="medium" >

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section id="start">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="item">
                                <img src={portfolio} alt="portfolio"/>
                                <div className="start-item">
                                    <h4><Rnum start={0} end={548} duration={1000} easing={'linear'} format={true} dot={0} lazy={true} /></h4>
                                    <h5>project completed</h5>
                                </div>
                            </div>
                            <div className="item">
                                <img src={clock} alt="clock"/>
                                <div className="start-item">
                                    <h4><Rnum start={0} end={1465} duration={1500} easing={'linear'} format={false} dot={0} lazy={true} /></h4>
                                    <h5>project completed</h5>
                                </div>
                            </div>
                            <div className="item">
                                <img src={star} alt="star"/>
                                <div className="start-item">
                                    <h4><Rnum start={0} end={612} duration={2000} easing={'linear'} format={true} dot={0} lazy={true} /></h4>
                                    <h5>project completed</h5>
                                </div>
                            </div>
                            <div className="item">
                                <img src={like} alt="like"/>
                                <div className="start-item">
                                    <h4><Rnum start={0} end={735} duration={2500} easing={'linear'} format={true} dot={0} lazy={true} /></h4>
                                    <h5>project completed</h5>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section id="our-works">
                    {this.props.products.slice(0,this.state.max_new).map((product,index) =>
                        <div key={index} className="photobox photobox_type16 item">
                            <div className="photobox__previewbox">
                                <img src={product.img}
                                     className="photobox__preview" alt="Preview" />
                                <span className="photobox__label"><Link to={"/single-project/?id="+index}><img src={look} alt=""/></Link></span>
                            </div>
                        </div>
                    )}
                </section>
                <button onClick={this.getMoreNews.bind(this)} className="load-more">
                    load more work
                </button>
                <section id="our-work-process">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="about-information">
                                <h2>About us</h2>
                                <p>Divide have don't man wherein air fourth. Own itself make have night won't make.
                                    A you under Seed appear which good give. Own give air without fowl moveth dry first
                                    heaven fruit, dominion she'd won't very all.
                                </p>
                            </div>
                            <iframe title="Video" src="https://www.youtube.com/embed/AJR_tkkK96w"  allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen>

                            </iframe>
                        </div>
                    </div>
                </section>
                <section id="spells">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="spell-item item-animation-1">
                                <img src={diamond} alt="diamond"/>
                                <h3>ui / ux design</h3>
                                <p>Be set fourth land god darkness make it wherein own</p>
                            </div>
                            <div className="spell-item item-animation-2">
                                <img src={arc} alt="arc"/>
                                <h3>web development</h3>
                                <p>A she'd them bring void moving third she'd kind fill</p>
                            </div>
                            <div className="spell-item item-animation-3">
                                <img src={phone} alt="phone"/>
                                <h3>app / mobile</h3>
                                <p>Dominion man second spirit he, earth they're creeping</p>
                            </div>
                            <div className="spell-item item-animation-4">
                                <img src={joy} alt="joy"/>
                                <h3>game design</h3>
                                <p>Morning his saying moveth it multiply appear life be</p>
                            </div>
                            <div className="spell-item item-animation-4">
                                <img src={plane} alt="joy"/>
                                <h3>SEO / marketing</h3>
                                <p>Give won't after land fill creeping meat you, may</p>
                            </div>
                            <div className="spell-item item-animation-3">
                                <img src={start2} alt="joy"/>
                                <h3>photography</h3>
                                <p>Creepeth one seas cattle grass give moving saw give</p>
                            </div>
                            <div className="spell-item item-animation-2">
                                <img src={wand} alt="joy"/>
                                <h3>game design</h3>
                                <p>Open, great whales air rule for, fourth life whales</p>
                            </div>
                            <div className="spell-item item-animation-1">
                                <img src={bucket} alt="joy"/>
                                <h3>illustrations</h3>
                                <p>Whales likeness hath, man kind for them air two won't</p>
                            </div>
                        </div>
                    </div>
                </section>
                <Slider/>
                <section id="our-sponsors">
                    <div className="container-x">
                        <div className="container-wrap">
                            <a href="/">
                                <img src={sponsor1} alt=""/>
                            </a>
                            <a href="/">
                                <img src={sponsor2} alt=""/>
                            </a>
                            <a href="/">
                                <img src={sponsor3} alt=""/>
                            </a>
                            <a href="/">
                                <img src={sponsor4} alt=""/>
                            </a>
                            <a href="/">
                                <img src={sponsor1} alt=""/>
                            </a>
                        </div>
                    </div>
                </section>
                <section id="feedback">
                    <div className="container">
                        <div className="container-wrap">
                            <div className="about-information">
                                <h2>Need a Project?</h2>
                                <p>Let us know what you're looking for in an agency. We'll take a look and see if this could be the start of something beautiful.</p>
                            </div>
                            <div className="form-between">
                                <input type="text" placeholder="Your name"/>
                                <input type="text" placeholder="Your Email"/>
                            </div>
                            <div className="form-around">
                                <input type="text" placeholder="Your title"/>
                                <textarea placeholder="Your comment" />
                            </div>
                            <button className="send-message">Send message</button>
                        </div>
                    </div>
                </section>
                <Footer/>
            </div>
        );
    }
}

export default connect(
    state => ({
        products: state
    }),
    dispatch => ({})
)(MainPage);