import React from 'react';
import {Link} from "react-router-dom";
import Footer from "../components/footer";
class Contact extends React.Component{
    render() {
        return (
            <div>
                <header id="header_">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="menu">
                                <div className="logo">
                                    <img src="" alt=""/>
                                </div>
                                <ul className="nav">
                                    <li><Link to="/">Home</Link></li>
                                    <li><Link to="/works/">Works</Link></li>
                                    <li><Link to="/contacts">Contact</Link></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </header>
                <section id="our-contacts">
                    <div className="container-x">
                        <div className="container-wrap">
                            <div className="our-contacts-info">
                                <h1>Contact info:</h1>
                                <p>To give give beginning divide, cattle. Give moving
                                    won't, there the abundantly she'd she'd brought air
                                    upon. Light hath subdue. Life days creature upon
                                    first heaven gathering dry.
                                </p>
                                <h5>Address: <span>Address:  10111 Santa Monica Boulevard, LA</span></h5>
                                <h5>Phone: <span>+44 987 065 908</span></h5>
                                <h5>Email: <span>info@Example.com</span></h5>
                                <h5>Fax: <span>+44 987 065 909</span></h5>
                            </div>
                            <div className="our-contacts-form">
                                <div className="form-between">
                                    <input type="text" placeholder="Your name"/>
                                    <input type="text" placeholder="Your Email"/>
                                </div>
                                <div className="form-around">
                                    <input type="text" placeholder="Your title"/>
                                    <textarea placeholder="Your comment" />
                                </div>
                                <button className="send-message">Send message</button>
                            </div>
                        </div>
                    </div>
                </section>
                <section id="map">
                    <div className="container-x">
                        <div className="container-wrap">
                            <iframe title="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d143543.23894017775!2d48.93322196648927!3d55.79561489263272!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x415ead2b7caccd99%3A0x7fcb77b9b5ad8c65!2z0JrQsNC30LDQvdGMLCDQoNC10YHQvy4g0KLQsNGC0LDRgNGB0YLQsNC9!5e0!3m2!1sru!2sru!4v1562328595259!5m2!1sru!2sru" allowFullScreen />
                        </div>
                    </div>
                </section>
                <Footer/>
            </div>
        )
    }
}
export default Contact;