import React from 'react';

import '../styles/default.css';

class Slider extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            timer: 7000,
            count_slider: 1
        }
    }

    slideNext(idification) {
        let thisItem = document.querySelector('.slider-item-'+ idification);
        let inactive = document.querySelector("#active");
        let thisNavigation = document.querySelector(".item-nav-"+idification);
        let inactiveNav = document.querySelector("#active-item-nav");
        if (thisItem.id !== "active") {
            this.setState({count_slide: this.state.count_slider = idification});
            inactive.id = "inactive";
            thisItem.id = "active";
            inactiveNav.id = "inactive-item-nav";
            thisNavigation.id = "active-item-nav";
            setTimeout(() => {
                document.querySelector("#inactive").id = "";
                document.querySelector("#inactive-item-nav").id = "";
            },500)
        }
    }
    sliderTimer() {
        setInterval(() => {
            if (this.state.count_slider >= document.querySelectorAll(".slider-item").length) {
                this.setState({count_slider: 1});
                document.querySelector('.slider-item-'+document.querySelectorAll(".slider-item").length).id = "inactive";
                document.querySelector('.slider-item-'+ 1).id = "active";
                document.querySelector('.item-nav-'+document.querySelectorAll(".item-nav").length).id = "inactive-item-nav";
                document.querySelector('.item-nav-'+ 1).id = "active-item-nav";
                setTimeout(() => {
                    document.querySelector("#inactive").id = "";
                    document.querySelector("#inactive-item-nav").id = "";
                },500);
            }else {
                //Работает правильно
                document.querySelector('.slider-item-'+ this.state.count_slider).id = "inactive";
                document.querySelector('.slider-item-'+ (this.state.count_slider+1)).id = "active";
                document.querySelector("#active-item-nav").id = "inactive-item-nav";
                document.querySelector(".item-nav-"+(this.state.count_slider + 1)).id = "active-item-nav";
                setTimeout(() => {
                    document.querySelector("#inactive").id = "";
                    document.querySelector("#inactive-item-nav").id = "";
                },500);
                this.setState({count_slider: this.state.count_slider += 1});
            }
        },this.state.timer)
    }
    componentDidMount() {
        this.sliderTimer();
    }

    render() {
        return (
            <section id="slider">
                <div className="slider-wrap">
                    <div className="slider-item-1 slider-item" id="active">
                        <h4>
                            “ Outstanding job and exceeded all expectations. It was a pleasure
                            to work with them on a sizable first project and am looking
                            forward to start the next one asap.”
                        </h4>
                        <b>Michael Hopkins</b>
                    </div>
                    <div className="slider-item-2 slider-item ">
                        <h4>
                            “ Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ad deserunt maxime mollitia totam voluptas. Blanditiis consectetur corporis dolorem error ex inventore, ipsam libero maiores molestias provident sequi tempore voluptas voluptatum!”
                        </h4>
                        <b>Michael Hopkins</b>
                    </div>
                    <div className="slider-item-3 slider-item ">
                        <h4>
                            “ Outstanding job and exceeded all expectations. It was a pleasure
                            to work with them on a sizable first project and am looking
                            forward to start the next one asap.”
                        </h4>
                        <b>Michael Hopkins</b>
                    </div>
                </div>
                <div className="slider-navigation">
                    <div className="item-nav item-nav-1" id="active-item-nav" onClick={this.slideNext.bind(this,1)} />
                    <div className="item-nav item-nav-2" id="" onClick={this.slideNext.bind(this,2)}/>
                    <div className="item-nav item-nav-3" id="" onClick={this.slideNext.bind(this,3)}/>
                </div>
            </section>
        )
    }
}
export default Slider;